<template>
  <div class="login">
    <h1>补充你的信息</h1>
    <img src="" alt="headpic" />
    <span class="info">姓名</span>
    <input type="text" placeholder="真实姓名" v-model="username" ></input>
    <input type="text" placeholder="手机号" v-model="phone"></input>
    <button class="btn btn-default" v-on:click="signUp">login</button>
  </div>
</template>

<script>

export default {
  data() {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
    }
  },
  methods: {
    signUp() {
      const phone = this.phone;
      const userInfo = localStorage.getItem('kf_userInfo');
      const user = Object.assign(userInfo,{phone});
      this.$http.put(`${config.route.user}${user._id}`, user, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      })
      .then((result) => {
        localStorage.setItem('kf_userInfo',result.body);
        this.$router.go('/profile');
      })
      .catch((err) => {
        console.log(err);
      })
    }
  }
}
</script>

<style scoped>
h1 {
  color: #42b983;
}

.avatar {
  width: 128px;
  height: 128px;
  margin: 0 auto;
  display: inline-block;
}

.info {
  background: yellow;
  width: 95%;
  padding: 0 10px;
  border: 1px solid black;
}
</style>



